.metric {
	margin: 0 -12px;
	padding: 59px 12px;
	background-color: $charcoal-grey;
	@include rmin($sm-to-md) {
		margin: 0;
		padding: 84px 32px; }
	@include rmin($md) {
		padding: 84px 80px; }
	@include rmin($lg) {
		padding: 92px 114px; }
	@include rmin($lg-to-xl) {
		display: flex;
		flex-wrap: wrap;
		padding: 92px 28px; }
	&__table {
		margin-bottom: 52px;
		@include rmin($lg-to-xl) {
			padding: 0 28px;
			flex-basis: 50%; } } }

.metric-data {
	margin-top: 30px;
	@include rmin($lg) {
		margin-top: 45px; }
	&__el {
		margin-bottom: 15px;
		color: $pale-grey;
		@include rmin($sm) {
			margin-bottom: 22px; }
		@include rmin($lg) {
			margin-bottom: 28px; }
		&_orange {
			color: $dusty-orange;
			.metric-data__graph {
				background: $dusty-orange; } }
		&_pink {
			color: $hot-pink;
			.metric-data__graph {
				background: $hot-pink; } }
		&_blue {
			color: $dark-sky-blue;
			.metric-data__graph {
				background: $dark-sky-blue; } }
		&_green {
			color: $mid-green;
			.metric-data__graph {
				background: $mid-green; } } }
	&__graph {
		width: 100%;
		height: 6px;
		margin-bottom: 3px;
		border-radius: 3px;
		background: $silver;
		@include rmin($sm) {
			margin-bottom: 5px; } }
	&__text {
		font-size: 23px;
		font-weight: 300;
		line-height: 1.39;
		letter-spacing: -0.6px;
		@include rmin($sm) {
			font-size: 26px;
			font-weight: 500;
			line-height: 1.23;
			letter-spacing: -0.4px; } } }
